<template>
  <el-container>
    <el-header>
      <div class="header">
        <div class="title">企业名称：中国平安</div>
      </div>
      <div class="" >
          <el-button type="primary" class="btn" @click="addServes">服务单新增</el-button>
          <el-button type="primary" class="btn">服务单导入</el-button>
      </div>
    </el-header>
    <el-main>
      <el-table :data="servesInfoList">
        <el-table-column prop="id" label="服务单ID">
        </el-table-column>
        <el-table-column prop="serveInfoList.userName" label="被服务人姓名">
        </el-table-column>
        <el-table-column prop="serveInfoList.mobile" label="被服务人电话">
        </el-table-column>
        <el-table-column prop="serveInfoList.mobile" label="关联权益人">
        </el-table-column>
        <el-table-column prop="serveInfoList.mobile" label="所属商品">
        </el-table-column>
        <el-table-column prop="serveInfoList.beginTime" label="生效起期">
        </el-table-column>
        <el-table-column prop="serveInfoList.endTime" label="生效至期">
        </el-table-column>
        <el-table-column prop="serveInfoList.status" label="生效状态">
        </el-table-column>
        <el-table-column label="操作" width='220'>
          <template slot-scope="scope">
            <el-button @click="handleClick(scope.row)" type="text" size="small">编辑</el-button>
            <el-button type="text" size="small" @click="goodsManageClick(scope.row)">删除</el-button>
          </template>
        </el-table-column>
      </el-table>
      <div class="demonstration-box" v-show="pageShow">
        <!-- <span class="demonstration">显示总数</span> -->
        <el-pagination
          @size-change="handleSizeChange"
          @current-change="handleCurrentChange"
          :current-page.sync="currentPage"
          :page-size="100"
          layout="total, prev, pager, next"
          :total="1000">
        </el-pagination>
      </div>
    </el-main>
  </el-container>
</template>
<script>
import { servesInfo } from '../../data/servesList'
import queryList from '../../utils/queryList'
export default {
  name: 'servesList',
  data() {
    return {
      servesInfoList: Array(8).fill(servesInfo),
      comName: '',
      value: '',
      currentPage: 1,
      pageShow: false
    }
  },
  methods: {
    addServes() {
      this.$router.push('addOrEditServe?type=add')
    },
    handleClick() {
      this.$router.push('addOrEditCom?type=edit&id=66')
    },
    handleSizeChange(val) {
        console.log(`每页 ${val} 条`);
      },
    handleCurrentChange(val) {
      console.log(`当前页: ${val}`);
    },
    goodsManageClick() {
      this.$router.push('../goodsManage/?comName=中国平安')
    },
    init() {
      const id = this.$route.query.id
      const param = {
        companyid: id,
        page: this.currentPage - 1,
        size: 20
      }
      queryList('order', param).then((res) => {
        this.serveInfoList = res.data.data.list
        this.pageShow = this.comInfoList.length > 20
      }).catch((err) => {
        console.log(err)
        this.$message(err.data.message)
      })
    }
  }
}
</script>
<style lang="less" scoped>
  .header {
    text-align: left;
    border-bottom: 1px solid #eee;
    margin-bottom: 10px
  }
  .com-input {
    width: 210px;
    margin-right: 20px
  }
  .btn{
    margin-left: 20px;
  }
</style>

